import { useState } from 'react';
import Link from 'next/link';

// 模拟的文章数据
const articles = [
  { id: 1, title: "Understanding React and Tailwind CSS", description: "A guide to integrating React with Tailwind CSS for fast, responsive UIs.", date: "2025-01-18", author: "John Doe" },
  { id: 2, title: "JavaScript ES6 Features You Should Know", description: "An overview of the most important ES6 features that every developer should understand.", date: "2025-01-15", author: "Jane Smith" },
  { id: 3, title: "Building a Personal Blog with Next.js", description: "Step-by-step guide to creating a fast, SEO-friendly blog with Next.js and React.", date: "2025-01-10", author: "Alice Johnson" },
  { id: 4, title: "CSS Grid: A Complete Guide", description: "Learn everything about CSS Grid and how it can be used for creating modern layouts.", date: "2025-01-05", author: "James Brown" },
  { id: 5, title: "Introduction to TypeScript", description: "A beginner's guide to TypeScript, how to set it up and get started.", date: "2025-01-02", author: "Mary Johnson" },
  { id: 6, title: "Next.js and Static Site Generation", description: "Learn how Next.js handles static site generation (SSG) and dynamic routes.", date: "2024-12-30", author: "Robert Lee" },
  { id: 7, title: "React Hook Form: A Deep Dive", description: "An in-depth guide to using React Hook Form for managing forms in React.", date: "2024-12-25", author: "Sophia Davis" },
  { id: 8, title: "Web Accessibility Best Practices", description: "A guide on how to make your website accessible for everyone, including people with disabilities.", date: "2024-12-20", author: "Michael Brown" },
  { id: 9, title: "Building a Portfolio with Next.js", description: "A complete guide to building your personal portfolio with Next.js.", date: "2024-12-15", author: "Emily Taylor" },
  { id: 10, title: "GraphQL vs REST: A Comparison", description: "Learn the differences between GraphQL and REST, and when to use each.", date: "2024-12-10", author: "David Wilson" },
  { id: 11, title: "Using Tailwind CSS with React", description: "How to integrate Tailwind CSS with React to create responsive and modern UIs.", date: "2024-12-05", author: "Anna Martinez" },
  { id: 12, title: "Performance Optimization in React", description: "Learn the best practices for optimizing React application performance.", date: "2024-12-01", author: "Chris Moore" }
];

const ArticlesGrid = () => {
  // 分页设置
  const articlesPerPage = 6;
  const [currentPage, setCurrentPage] = useState(1);

  // 计算当前页需要展示的文章
  const indexOfLastArticle = currentPage * articlesPerPage;
  const indexOfFirstArticle = indexOfLastArticle - articlesPerPage;
  const currentArticles = articles.slice(indexOfFirstArticle, indexOfLastArticle);

  // 计算总页数
  const totalPages = Math.ceil(articles.length / articlesPerPage);

  // 生成分页按钮
  const paginate = (pageNumber: number) => setCurrentPage(pageNumber);

  return (
    <div>
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8">
        {currentArticles.map((article) => (
          <div key={article.id} className="card shadow-xl bg-base-100 hover:bg-gray-100 transition-all duration-300">
            <div className="card-body">
              <h2 className="card-title text-xl font-bold">{article.title}</h2>
              <p className="text-sm text-gray-500">{article.date}</p>
              <p className="mt-4">{article.description}</p>
              <p className="mt-2 text-sm text-gray-600">Written by {article.author}</p>
              <div className="card-actions justify-end">
                {/* "Read More" 按钮，链接到文章详细页面 */}
                <Link href={`/articles/${article.id}`}>
                  <a className="btn btn-primary">Read More</a>
                </Link>
              </div>
            </div>
          </div>
        ))}
      </div>

      {/* 分页按钮 */}
      <div className="flex justify-center mt-8">
        <div className="btn-group">
          {/* Previous Button */}
          <button
            className={`btn ${currentPage === 1 ? 'btn-disabled' : ''}`}
            onClick={() => paginate(currentPage - 1)}
            disabled={currentPage === 1}
          >
            Previous
          </button>

          {/* 页面数字 */}
          {Array.from({ length: totalPages }).map((_, index) => (
            <button
              key={index}
              className={`btn ${currentPage === index + 1 ? 'btn-active' : ''}`}
              onClick={() => paginate(index + 1)}
            >
              {index + 1}
            </button>
          ))}

          {/* Next Button */}
          <button
            className={`btn ${currentPage === totalPages ? 'btn-disabled' : ''}`}
            onClick={() => paginate(currentPage + 1)}
            disabled={currentPage === totalPages}
          >
            Next
          </button>
        </div>
      </div>
    </div>
  );
};

export default ArticlesGrid;
